import React, { Component } from 'react';
import styled from 'styled-components'
import Hero from '../components/Hero'
import Header from '../components/Header'

const PageWrapper = styled.div`
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
`;

const Floaty = styled.div`
  width: 40vw;
  height: auto;
  margin: 24px auto;
  padding: 24px;
  box-sizing: border-box;
  background: #0f0f0f;
`;

const FloatyContent = styled.p`
  font-size: 1.5rem;
  font-weight: 500;
  color: #fff;
  margin: 0 0 24px 0;
  padding: 0;
`;

const FloatyButton = styled.a`
  width: 100%;
  height: 40px;
  background: #fff;
  color: #0f0f0f;
`;

class Index extends Component {

  constructor(props) {
    super(props);
    this.state = {
      menuVisible: false
    }
  }

  render() {
    return (
      <PageWrapper>
        <Header handleMenuToggle={() => this.setState({ menuVisible: !this.state.menuVisible})} open={this.state.menuVisible}/>
        <Hero />
        <Floaty className="about">
          <FloatyContent className="about__content">
            Bland is an agile marketing agency devoted to first impressions. We sweat the details of content and identity to create tangible and expansive presences that capture customers and lead to real-world growth.
          </FloatyContent>
          <FloatyButton className="about__button">
            See what we can do
          </FloatyButton>
        </Floaty>
        <Hero></Hero>
      </PageWrapper>
    );
  }

}

export default Index;



